import React from 'react';
import Evmenu from './eagle-vision/ev-menu/Evmenu';
import {Container, Row, Col, Form, Button, Image, Spinner} from 'react-bootstrap';
import {db} from './services/firestore';

class App extends React.Component{
    constructor(props){
        super(props);
        this.state = {
           'logged': false
        }

        this.getLogin = this.getLogin.bind(this);
        this.aprobed = this.aprobed.bind(this);
        this.desaprobed = this.desaprobed.bind(this);
    }

    componentDidMount(){
        this.compruebaCookie();
    }

    compruebaCookie(){
        var logueado = localStorage.getItem('userlogged');

        if (logueado === 'true'){
            this.aprobed();
        }
    }

    getLogin(){
        var name = document.querySelector('#formUser').value;        
        var comprueba = false;       

        db.collection("users").where("user", "==", name)
        .get()
        .then(function(querySnapshot) {
                querySnapshot.forEach(function(doc) {
                var dbdata = doc.data().password;
                var password = document.querySelector('#formPassword').value;
                
                if(dbdata === password)
                    comprueba = true;
            });            
        })
        .catch(function(error) {
            console.log("Error getting documents: ", error);
        }).then(() => {
            if (comprueba === true){
                localStorage.setItem('userlogged', true);
                this.aprobed();
            }else{
                alert('Contraseña o usuario inválido.')
            }                
        });               
    }

    aprobed(){
        this.setState(state => ({
            'logged': true,
        }))
    }

    desaprobed(){
        localStorage.setItem('userlogged', false);
        this.setState({
            'logged': false
        });
    }

    render(){
        if(!this.state.logged){
            return(
                <div>
                    <Container fluid className="py-5">
                        <Row className="py-5">
                            <Col className="px-5 py-5">
                                <h1 className="text-center">Eagle Vision</h1>
                                <Form className="py-5">                                
                                    <div className="text-center">
                                        <Spinner animation="grow" />
                                    </div>
                                    <Form.Group controlId="formUser">
                                        <Form.Label>Usuario</Form.Label>
                                        <Form.Control type="text" placeholder="Introduce tu usuario." />
                                        <Form.Text className="text-muted">
                                            Recuerda que tus credenciales son confidenciales, compartirlas puede llevarte a la muerte.
                                        </Form.Text>
                                    </Form.Group>
    
                                    <Form.Group controlId="formPassword">
                                        <Form.Label>Contraseña</Form.Label>
                                        <Form.Control type="password" placeholder="Introduce tu contraseña." />
                                    </Form.Group>
                                    <div className="text-center">
                                        <Button variant="dark" onClick={this.getLogin}>
                                            Entrar
                                        </Button>
                                    </div>
                                </Form>
                            </Col>
                            <Col>
                                <Image src="https://i.imgur.com/MkfMve5.jpg" width="100%"></Image>
                            </Col>
                        </Row>
                    </Container>
                </div>
            )
        }else{
            return(
                <div>
                    <p className="text-right p-0 m-1">
                        <Button className="btn-sm" variant="danger" onClick={this.desaprobed}>
                            Salir
                            <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-lock-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                <path d="M2.5 9a2 2 0 0 1 2-2h7a2 2 0 0 1 2 2v5a2 2 0 0 1-2 2h-7a2 2 0 0 1-2-2V9z"/>
                                <path fillRule="evenodd" d="M4.5 4a3.5 3.5 0 1 1 7 0v3h-1V4a2.5 2.5 0 0 0-5 0v3h-1V4z"/>
                            </svg>
                        </Button>
                    </p>
                    <Evmenu></Evmenu>
                </div>
            )
        }        
    }
}

export default App;