import React from 'react';
import { Button, Modal, Form } from 'react-bootstrap';
import {db} from '../../../services/firestore.js';


function MyVerticallyCenteredModal(props) {
  
  var old = props.datos.datos;

  function Editmgmodal(){
    // Datos metidos en el formulario:
    var name = document.querySelector('#formNameMG').value;
    var leader = document.querySelector('#formLeaderMG').value;
    var location = document.querySelector('#formLocationMG').value;
    var location_pic = document.querySelector('#formLocationPicMG').value;
    var description = document.querySelector('#formDescriptionMG').value;
    var drugs_desc = document.querySelector('#formDrugsDescriptionMG').value;
    var guns_desc = document.querySelector('#formGunsDescriptionMG').value;
    var friends = document.querySelector('#formFriendsDescriptionMG').value;
    var grade = document.querySelector('#formGradeDescriptionMG').value;
    var relationship = document.querySelector('#formRelationshipDescriptionMG').value;

    // Condicionales al formulario:
    var drugs_yn = drugs_desc ? true : false;
    var guns_yn = guns_desc ? true : false;

    db.collection("mafia_gang").where("name", "==", name)
    .get()
    .then(querySnapshot => {
      querySnapshot.forEach(function(doc) {
        
        db.collection("mafia_gang").doc(doc.id).set({
          'description': description,
          'drugs_desc': drugs_desc,
          'drugs_yn' : drugs_yn,
          'friends' : friends,
          'grade' : grade,
          'guns_desc' : guns_desc,
          'guns_yn' : guns_yn,
          'name': name,
          'leader': leader,
          'location': location,
          'location_pic': location_pic,
          'relationship' : relationship
        })
        .then(function() {
          console.log("Document successfully written!");
          window.location.reload(false);
        })
        .catch(function(error) {
          console.error("Error writing document: ", error);
        });
      })
    })
    .catch(function(error) {
        console.log("Error getting documents: ", error);
    });
  }

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Editar Mafia / Banda
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Form.Group controlId="formNameMG">
            <Form.Label>Nombre de la Mafia / Banda:</Form.Label>
            <Form.Control type="text" defaultValue={old.name} />
          </Form.Group>
          <Form.Group controlId="formLeaderMG">
            <Form.Label>Líder:</Form.Label>
            <Form.Control type="text" defaultValue={old.leader} />
          </Form.Group>
          <Form.Group controlId="formDescriptionMG">
            <Form.Label>Descripción:</Form.Label>
            <Form.Control as="textarea" rows={3} defaultValue={old.description} />
          </Form.Group>
          <Form.Group controlId="formLocationMG">
            <Form.Label>Localización:</Form.Label>
            <Form.Control as="textarea" rows={3} defaultValue={old.location} />
          </Form.Group>
          <Form.Group controlId="formLocationPicMG">
            <Form.Label>Imagen de Localización:</Form.Label>
            <Form.Control type="text" defaultValue={old.location_pic} />
          </Form.Group>
          <Form.Group controlId="formDrugsDescriptionMG">
            <Form.Label>Drogas:</Form.Label>
            <Form.Control as="textarea" rows={3} defaultValue={old.drugs_desc}/>
          </Form.Group>
          <Form.Group controlId="formGunsDescriptionMG">
            <Form.Label>Armas:</Form.Label>
            <Form.Control as="textarea" rows={3} defaultValue={old.guns_desc} />
          </Form.Group>
          <Form.Group controlId="formFriendsDescriptionMG">
            <Form.Label>Amigos o Enemigos:</Form.Label>
            <Form.Control as="textarea" rows={3} defaultValue={old.friends}/>
          </Form.Group>
          <Form.Group controlId="formGradeDescriptionMG">
            <Form.Label>Rangos:</Form.Label>
            <Form.Control as="textarea" rows={3} defaultValue={old.grade}/>
          </Form.Group>
          <Form.Group controlId="formRelationshipDescriptionMG">
            <Form.Label>Relación:</Form.Label>
            <Form.Control as="select" defaultValue={old.relationship}>
              <option value="Amiga">Amiga</option>
              <option value="Enemiga">Enemiga</option>
              <option value="Neutral">Neutral</option>
            </Form.Control>
          </Form.Group>
          
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="dark" onClick={() => Editmgmodal()} onClickCapture={props.onHide}>Guardar</Button>
        <Button variant="secondary" onClick={props.onHide}>Cerrar</Button>
      </Modal.Footer>
    </Modal>
  );
}
  
function Editmgmodal(datostabla) {
  const [modalShow, setModalShow] = React.useState(false);

  return (
    <>
      <Button variant="light" className="btn-sm" onClick={() => setModalShow(true)}>
        <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-pencil-square" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456l-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"/>
          <path fillRule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"/>
        </svg>
      </Button>

      <MyVerticallyCenteredModal
        datos={datostabla}
        show={modalShow}
        onHide={() => setModalShow(false)}
      />
    </>
  );
}

export default Editmgmodal;