import React from 'react';
import {Button, Modal, Form } from 'react-bootstrap';
import {db} from '../../../services/firestore.js';


function MyVerticallyCenteredModal(props) {  

  function createOb(){

    // Datos metidos en el formulario:
    var attitude = document.querySelector('#formAttitude').value;
    var description = document.querySelector('#formDescriptionOB').value;
    var description_pic = document.querySelector('#formDescriptionPicOB').value;
    var friends = document.querySelector('#formFriendsOB').value;
    var location = document.querySelector('#formLocationOB').value;
    var location_pic = document.querySelector('#formLocationPicOB').value;
    var mg = document.querySelector('#formMGOB').value;
    var name = document.querySelector('#formNameOB').value;
    var priority = document.querySelector('#formPriorityOB').value;
    var relationship = document.querySelector('#formRelationshipOB').value;
    var vehicle_desc = document.querySelector('#formVehicleDescription').value;
    var vehicle_model = document.querySelector('#formVehicleModel').value;
    var vehicle_pic = document.querySelector('#formVehiclePic').value;
   
    db.collection("objectives").doc().set({
      'attitude': attitude,
      'description': description,
      'description_pic': description_pic,
      'friends': friends,
      'location': location,
      'location_pic': location_pic,
      'mg': mg,
      'name': name,
      'priority': priority,
      'relationship': relationship,
      'vehicle_desc': vehicle_desc,
      'vehicle_model': vehicle_model,
      'vehicle_pic': vehicle_pic
    })
    .then(function() {
        console.log("Document successfully written!");
        window.location.reload(false);
    })
    .catch(function(error) {
        console.error("Error writing document: ", error);
    });
  }

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Crear Objetivo
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Form.Group controlId="formNameOB">
            <Form.Label>Nombre:</Form.Label>
            <Form.Control type="text" placeholder="Escribe aquí el nombre del objetivo." />
          </Form.Group>
          <Form.Group controlId="formMGOB">
            <Form.Label>Mafia o Banda:</Form.Label>
            <Form.Control type="text" placeholder="Escribe aquí el nombre de la Mafia o Banda a la que pertenece." />
          </Form.Group>
          <Form.Group controlId="formDescriptionOB">
            <Form.Label>Descripción:</Form.Label>
            <Form.Control as="textarea" rows={3} placeholder="Escribe una descripción general del objetivo." />
          </Form.Group>          
          <Form.Group controlId="formDescriptionPicOB">
            <Form.Label>Imagen del Objetivo:</Form.Label>
            <Form.Control type="text" placeholder="Añade la url de la imagen." />
          </Form.Group>
          <Form.Group controlId="formAttitude">
            <Form.Label>Actitud:</Form.Label>
            <Form.Control as="select" defaultValue="Pacífica">
              <option value="Pacífica">Pacífica</option>
              <option value="Agresiva">Agresiva</option>
              <option value="Neutral">Neutral</option>
            </Form.Control>
          </Form.Group>
          <Form.Group controlId="formLocationOB">
            <Form.Label>Localización:</Form.Label>
            <Form.Control as="textarea" rows={3} placeholder="Describe la información general que tienes sobre la Localización del Objetivo."/>
          </Form.Group>
          <Form.Group controlId="formLocationPicOB">
            <Form.Label>Imagen de Localización:</Form.Label>
            <Form.Control type="text" placeholder="Añade la url de la imagen." />
          </Form.Group>
          <Form.Group controlId="formVehicleDescription">
            <Form.Label>Descripción del vehículo:</Form.Label>
            <Form.Control as="textarea" rows={3} placeholder="Describe la información general sobre el vehículo del objetivo."/>
          </Form.Group>
          <Form.Group controlId="formVehicleModel">
            <Form.Label>Modelo del vehículo:</Form.Label>
            <Form.Control as="textarea" rows={3} placeholder="Añade el modelo del vehículo." />
          </Form.Group>
          <Form.Group controlId="formVehiclePic">
            <Form.Label>Imagen del vehículo:</Form.Label>
            <Form.Control type="text" placeholder="Añade la url de la imagen."/>
          </Form.Group>
          <Form.Group controlId="formFriendsOB">
            <Form.Label>Aliados o Rivales:</Form.Label>
            <Form.Control as="textarea" rows={3} placeholder="Describe la información que tienes sobre los posibles aliados o rivales del Objetivo." />
          </Form.Group>         
          <Form.Group controlId="formRelationshipOB">
            <Form.Label>Relación:</Form.Label>
            <Form.Control as="select" defaultValue="Amiga">
              <option value="Amiga">Amiga</option>
              <option value="Enemiga">Enemiga</option>
              <option value="Neutral">Neutral</option>
            </Form.Control>
          </Form.Group>
          <Form.Group controlId="formPriorityOB">
            <Form.Label>Prioridad:</Form.Label>
            <Form.Control as="select" defaultValue="Alta">
              <option value="Alta">Alta</option>
              <option value="Media">Media</option>
              <option value="Baja">Baja</option>
            </Form.Control>
          </Form.Group>          
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="dark" onClick={() => createOb(props)} onClickCapture={props.onHide}>Guardar</Button>
        <Button variant="secondary" onClick={props.onHide}>Cerrar</Button>
      </Modal.Footer>
    </Modal>
  );
}
  
function Createobmodal(datos) {
  const [modalShow, setModalShow] = React.useState(false);

  return (
    <>
      <Button variant="dark" className="btn-sm" onClick={() => setModalShow(true)}>
        <svg width="3em" height="3em" viewBox="0 0 16 16" className="bi bi-plus-circle-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path fillRule="evenodd" d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8.5 4.5a.5.5 0 0 0-1 0v3h-3a.5.5 0 0 0 0 1h3v3a.5.5 0 0 0 1 0v-3h3a.5.5 0 0 0 0-1h-3v-3z"/>
        </svg>
      </Button>

      <MyVerticallyCenteredModal
        show={modalShow}
        onHide={() => setModalShow(false)}
      />
    </>
  );
}

export default Createobmodal;