import React from 'react';
import { Button, Modal } from 'react-bootstrap';
import {db} from '../../../services/firestore.js';


function MyVerticallyCenteredModal(props) {

  function deleteMg(name){

    db.collection("mafia_gang").where("name", "==", name)
    .get()
    .then(function(querySnapshot) {
        querySnapshot.forEach(function(doc) {
            db.collection("mafia_gang").doc(doc.id).delete().then(function() {
                console.log("Document successfully deleted!");
                window.location.reload(false);
            }).catch(function(error) {
                console.error("Error removing document: ", error);
            });
        });
    })
    .catch(function(error) {
        console.log("Error getting documents: ", error);
    });
  }

  return (
    <Modal
      {...props}
      size="m"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header className="bg-danger" closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Eliminar: {props.nameMg}
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        Estás intentando <b>eliminar información de los archivos clasificados</b>, ¿estás segur@?
      </Modal.Body>
      <Modal.Footer>
        <Button variant="danger" onClick={() => deleteMg(props.nameMg)} onClickCapture={props.onHide}>Eliminar</Button>
        <Button variant="secondary" onClick={props.onHide}>Cancelar</Button>
      </Modal.Footer>
    </Modal>
  );
}
  
function Editmgmodal(datos) {
  const [modalShow, setModalShow] = React.useState(false);

  return (
    <>
      <Button variant="danger" className="btn-sm mx-3" onClick={() => setModalShow(true)}>
        <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-x-square-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path fillRule="evenodd" d="M2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2zm3.354 4.646a.5.5 0 1 0-.708.708L7.293 8l-2.647 2.646a.5.5 0 0 0 .708.708L8 8.707l2.646 2.647a.5.5 0 0 0 .708-.708L8.707 8l2.647-2.646a.5.5 0 0 0-.708-.708L8 7.293 5.354 4.646z"/>
        </svg>
      </Button>

      <MyVerticallyCenteredModal
        nameMg={datos.datos}
        show={modalShow}
        onHide={() => setModalShow(false)}
      />
    </>
  );
}

export default Editmgmodal;