import {bd, storage} from '../services/firestore';

var urlImg;

function UploadImg(file, nameFile){

}

function ShowImg(proyectLocation, treeFile){
    // var gsReference = storage.refFromURL('gs://inteligencia-ae.appspot.com/');
    var gsReference = storage.refFromURL(proyectLocation);
    
    // gsReference.child('base-img/prueba.jpg').getDownloadURL().then(function(url) {
    //     return(url);
    // });

    gsReference.child(treeFile).getDownloadURL().then(function(url){
        urlImg = url;
    }).catch(function(error){
        console.log(error);
    }).finally((url) => {
        return urlImg;
    });
}


export default(UploadImg,ShowImg)