import React from 'react';
import {Button, Modal, Form } from 'react-bootstrap';
import {db} from '../../../services/firestore.js';


function MyVerticallyCenteredModal(props) {  

  function createMg(datos_tabla){

    // Datos metidos en el formulario:
    var name = document.querySelector('#formNameMG').value;
    var leader = document.querySelector('#formLeaderMG').value;
    var description = document.querySelector('#formDescriptionMG').value;
    var location = document.querySelector('#formLocationMG').value;
    var location_pic = document.querySelector('#formLocationPicMG').value;
    var drugs_desc = document.querySelector('#formDrugsDescriptionMG').value;
    var guns_desc = document.querySelector('#formGunsDescriptionMG').value;
    var friends = document.querySelector('#formFriendsDescriptionMG').value;
    var grade = document.querySelector('#formGradeDescriptionMG').value;
    var relationship = document.querySelector('#formRelationshipDescriptionMG').value;

    // Condicionales al formulario:
    var drugs_yn = drugs_desc ? true : false;
    var guns_yn = guns_desc ? true : false;
   
    db.collection("mafia_gang").doc().set({
      'description': description,
      'drugs_desc': drugs_desc,
      'drugs_yn' : drugs_yn,
      'friends' : friends,
      'grade' : grade,
      'guns_desc' : guns_desc,
      'guns_yn' : guns_yn,
      'name': name,
      'leader': leader,
      'location': location,
      'location_pic': location_pic,
      'relationship' : relationship
    })
    .then(function() {
        console.log("Document successfully written!");
        window.location.reload(false);
    })
    .catch(function(error) {
        console.error("Error writing document: ", error);
    });
    
  }

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Crear Mafia / Banda
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Form.Group controlId="formNameMG">
            <Form.Label>Nombre de la Mafia / Banda:</Form.Label>
            <Form.Control type="text" required placeholder="Escribe aquí el nombre de la Mafia o Banda." />
          </Form.Group>
          <Form.Group controlId="formLeaderMG">
            <Form.Label>Líder:</Form.Label>
            <Form.Control type="text" placeholder="Escribe aquí el nombre de el/la líder la Mafia o Banda." />
          </Form.Group>
          <Form.Group controlId="formDescriptionMG">
            <Form.Label>Descripción:</Form.Label>
            <Form.Control as="textarea" rows={3} placeholder="Describe la información general que tienes sobre la Mafia o Banda." />
          </Form.Group>
          <Form.Group controlId="formLocationMG">
            <Form.Label>Localización:</Form.Label>
            <Form.Control as="textarea" rows={3} placeholder="Describe la información general que tienes sobre la Localización de la Mafia o Banda." />
          </Form.Group>
          <Form.Group controlId="formLocationPicMG">
            <Form.Label>Imagen de Localización:</Form.Label>
            <Form.Control type="text" placeholder="Añade la url de la imagen." />
          </Form.Group>
          <Form.Group controlId="formDrugsDescriptionMG">
            <Form.Label>Drogas:</Form.Label>
            <Form.Control as="textarea" rows={3} placeholder="Describe la información que tienes sobre las drogas en poder de la Mafia o Banda."/>
          </Form.Group>
          <Form.Group controlId="formGunsDescriptionMG">
            <Form.Label>Armas:</Form.Label>
            <Form.Control as="textarea" rows={3} placeholder="Describe la información que tienes sobre las armas en poder de la Mafia o Banda."/>
          </Form.Group>
          <Form.Group controlId="formFriendsDescriptionMG">
            <Form.Label>Amigos o Enemigos:</Form.Label>
            <Form.Control as="textarea" rows={3} placeholder="Describe la información que tienes sobre los posibles aliados o rivales de la Mafia o Banda."/>
          </Form.Group>
          <Form.Group controlId="formGradeDescriptionMG">
            <Form.Label>Rangos:</Form.Label>
            <Form.Control as="textarea" rows={3} placeholder="Describe la información que tienes sobre los distintos papeles de poder en la Mafia o Banda." />
          </Form.Group>
          <Form.Group controlId="formRelationshipDescriptionMG">
            <Form.Label>Relación:</Form.Label>
            <Form.Control as="select">
              <option value="Amiga">Amiga</option>
              <option value="Enemiga">Enemiga</option>
              <option value="Neutral">Neutral</option>
            </Form.Control>
          </Form.Group>          
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="dark" onClick={() => createMg(props)} onClickCapture={props.onHide}>Guardar</Button>
        <Button variant="secondary" onClick={props.onHide}>Cerrar</Button>
      </Modal.Footer>
    </Modal>
  );
}
  
function Createmgmodal(datos) {
  const [modalShow, setModalShow] = React.useState(false);

  return (
    <>
      <Button variant="dark" className="btn-sm" onClick={() => setModalShow(true)}>
        <svg width="3em" height="3em" viewBox="0 0 16 16" className="bi bi-plus-circle-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path fillRule="evenodd" d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8.5 4.5a.5.5 0 0 0-1 0v3h-3a.5.5 0 0 0 0 1h3v3a.5.5 0 0 0 1 0v-3h3a.5.5 0 0 0 0-1h-3v-3z"/>
        </svg>
      </Button>

      <MyVerticallyCenteredModal
        show={modalShow}
        onHide={() => setModalShow(false)}
      />
    </>
  );
}

export default Createmgmodal;