import React from 'react';
import { Tab, Row, Col, Nav, Dropdown, Table, Card, Image } from 'react-bootstrap';
import {db} from '../../services/firestore.js';
import Editmgmodal from '../ev-intelligence/mafia-gang/Editmgmodal.js';
import Infomgmodal from '../ev-intelligence/mafia-gang/Infomgmodal.js';
import Createmgmodal from '../ev-intelligence/mafia-gang/Createmgmodal.js';
import Deletemgmodal from '../ev-intelligence/mafia-gang/Deletemgmodal.js';
import Createobmodal from '../ev-intelligence/objetive/Createobmodal.js';
import Infoobmodal from '../ev-intelligence/objetive/Infoobmodal.js';
import Editobmodal from '../ev-intelligence/objetive/Editobmodal.js';
import Deleteobmodal from '../ev-intelligence/objetive/Deleteobmodal.js';
import './Evmenu.css';
import {ShowImg} from './../../evilqueen/blackMagic';

class Evmenu extends React.Component{
    constructor(props){
        super(props);
        this.state = {
           'mg': [],
           'ob': []
        }
    }

    componentDidMount(){
        // get the whole collection
        db.collection("mafia_gang").orderBy('name')
        .get()
        .then(querySnapshot => {
            var data = querySnapshot.docs.map(doc => doc.data());
            
            this.setState({
                'mg': data
            })
        });

        db.collection("objectives").orderBy('name')
        .get()
        .then(querySnapshot => {
            var data = querySnapshot.docs.map(doc => doc.data());

            this.setState({
                'ob': data
            })
        });
    }

    render(){
        if(this.state.mg){

            const datostablaMg = this.state.mg;
            const datostablaOb = this.state.ob;

            return (
                <div>
                    <Tab.Container id="left-tabs-example" defaultActiveKey="mafia-gang">
                    <Row>
                    <Col sm={3} className="bg-light">
                        {/* Imagen del menu */}
                        <div className="text-center">
                            Foto del menú
                        </div>
                        {/* <Image src="./../img/reportada.jpg" roundedCircle/> */}
                        <br></br>
                        {/* Opciones del menu */}
                        <Nav variant="tabs" className="flex-column">
                            <h5 className="mb-1 text-secondary">Inteligencia</h5>
                            <Nav.Item>
                                <Nav.Link eventKey="mafia-gang">Mafias / Bandas</Nav.Link>
                            </Nav.Item>
                            <Nav.Item>
                                <Nav.Link eventKey="objectives">Objetivos</Nav.Link>
                            </Nav.Item>
            
                            <Dropdown.Divider />
                            <h5 className="mb-1 text-secondary">Intendencia</h5>                
                            <Nav.Item>
                                <Nav.Link eventKey="drugs">Drogas</Nav.Link>
                            </Nav.Item>
                            <Nav.Item>
                                <Nav.Link eventKey="guns">Armas</Nav.Link>
                            </Nav.Item>
                                        
                            <Dropdown.Divider />
                            <h5 className="mb-1 text-secondary">Operaciones</h5>
                            <Nav.Item>
                                <Nav.Link eventKey="estate">Propiedades</Nav.Link>
                            </Nav.Item>
                            <Nav.Item>
                                <Nav.Link eventKey="vehicles">Vehículos</Nav.Link>
                            </Nav.Item>
                        </Nav>                    
                    </Col>
                    <Col sm={9} className="p-0 m-0">
                        <Image src="https://i.imgur.com/ebeXKVX.jpg" width="100%"></Image> 

                        <Tab.Content>
                        <Tab.Pane eventKey="mafia-gang">
                            {/* Botón de crear mafia */}
                            <p className="text-right m-2">
                                <Createmgmodal datos={datostablaMg}></Createmgmodal>
                            </p>

                            {/* Tabla de las mafias */}
                            <Table bordered hover>
                                <thead>
                                    <tr className="text-center thead-dark">
                                        <th>Nombre</th>
                                        <th>Líder</th>
                                        <th>Nº Miembros Conocidos</th>
                                        <th>Armas</th>
                                        <th>Drogas</th>
                                        <th>Relación</th>
                                        <th>Editar/Eliminar</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {datostablaMg.map((listValue, index) => {
                                        var color;
                                        
                                        if(listValue.relationship === 'Amiga')
                                            color = 'table-success';
                                        
                                        if (listValue.relationship === 'Enemiga')
                                            color = 'table-danger';

                                        return(
                                            <tr key={index} className={color}>
                                                <td><b>{listValue.name}</b> <Infomgmodal datos={datostablaMg[index]}></Infomgmodal></td>
                                                <td>{listValue.leader ? listValue.leader : 'No conocido'}</td>
                                                <td>{listValue.n_members ? listValue.n_members : 'No conocido'}</td>
                                                <td>{listValue.guns_yn ? 'Sí' : 'No / No conocido'}</td>
                                                <td>{listValue.drugs_yn ? 'Sí' : 'No / No conocido'}</td>
                                                <td>{listValue.relationship}</td>
                                                <td>
                                                    <Editmgmodal datos={datostablaMg[index]}></Editmgmodal>
                                                    <Deletemgmodal datos={datostablaMg[index].name}></Deletemgmodal>
                                                </td>
                                            </tr>
                                        );
                                    })}
                                </tbody>
                            </Table>
                        </Tab.Pane>
                        <Tab.Pane eventKey="objectives">
                            {/* Botón de crear mafia */}
                            <p className="text-right m-2">
                                <Createobmodal datos={datostablaOb}></Createobmodal>
                            </p>

                            {/* Tabla de las mafias */}
                            <Table bordered hover>
                                <thead>
                                    <tr className="text-center thead-dark">
                                        <th>Nombre</th>
                                        <th>Mafia o Banda</th>
                                        <th>Vehículo</th>
                                        <th>Actitud</th>                                        
                                        <th>Relación</th>
                                        <th>Prioridad</th>                                        
                                        <th>Editar/Eliminar</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {datostablaOb.map((listValue, index) => {
                                        var color;
                                        
                                        if(listValue.relationship === 'Amiga')
                                            color = 'table-success';
                                        
                                        if (listValue.relationship === 'Enemiga')
                                            color = 'table-danger';

                                        return(
                                            <tr key={index} className={color}>
                                                <td><b>{listValue.name}</b> <Infoobmodal datos={datostablaOb[index]}></Infoobmodal></td>
                                                <td>{listValue.mg ? listValue.mg : 'No conocido'}</td>
                                                <td>{listValue.vehicle_model ? listValue.vehicle_model : 'No / No conocido'}</td> 
                                                <td className="text-center">
                                                    {(listValue.attitude === 'Pacífica') ? <svg width="2em" height="2em" viewBox="0 0 16 16" className="bi bi-emoji-laughing-fill text-success" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                                                                <path fillRule="evenodd" d="M8 16A8 8 0 1 0 8 0a8 8 0 0 0 0 16zM7 6.5c0 .501-.164.396-.415.235C6.42 6.629 6.218 6.5 6 6.5c-.218 0-.42.13-.585.235C5.164 6.896 5 7 5 6.5 5 5.672 5.448 5 6 5s1 .672 1 1.5zm5.331 3a1 1 0 0 1 0 1A4.998 4.998 0 0 1 8 13a4.998 4.998 0 0 1-4.33-2.5A1 1 0 0 1 4.535 9h6.93a1 1 0 0 1 .866.5zm-1.746-2.765C10.836 6.896 11 7 11 6.5c0-.828-.448-1.5-1-1.5s-1 .672-1 1.5c0 .501.164.396.415.235.165-.106.367-.235.585-.235.218 0 .42.13.585.235z"/>
                                                                                            </svg> : ''}
                                                    {(listValue.attitude === 'Agresiva') ? <svg width="2em" height="2em" viewBox="0 0 16 16" className="bi bi-emoji-angry-fill text-danger" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                                                                <path fillRule="evenodd" d="M8 16A8 8 0 1 0 8 0a8 8 0 0 0 0 16zM4.053 4.276a.5.5 0 0 1 .67-.223l2 1a.5.5 0 0 1 .166.76c.071.206.111.44.111.687C7 7.328 6.552 8 6 8s-1-.672-1-1.5c0-.408.109-.778.285-1.049l-1.009-.504a.5.5 0 0 1-.223-.67zm.232 8.157a.5.5 0 0 0 .683-.183A3.498 3.498 0 0 1 8 10.5c1.295 0 2.426.703 3.032 1.75a.5.5 0 1 0 .866-.5A4.498 4.498 0 0 0 8 9.5a4.5 4.5 0 0 0-3.898 2.25.5.5 0 0 0 .183.683zM10 8c.552 0 1-.672 1-1.5 0-.408-.109-.778-.285-1.049l1.009-.504a.5.5 0 1 0-.448-.894l-2 1a.5.5 0 0 0-.165.76A2.12 2.12 0 0 0 9 6.5c0 .828.448 1.5 1 1.5z"/>
                                                                                            </svg> : ''}
                                                    {(listValue.attitude === 'Neutral') ? <svg width="2em" height="2em" viewBox="0 0 16 16" className="bi bi-emoji-neutral-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                                                            <path fillRule="evenodd" d="M8 16A8 8 0 1 0 8 0a8 8 0 0 0 0 16zM7 6.5C7 7.328 6.552 8 6 8s-1-.672-1-1.5S5.448 5 6 5s1 .672 1 1.5zm-3 4a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 0 1h-7a.5.5 0 0 1-.5-.5zM10 8c.552 0 1-.672 1-1.5S10.552 5 10 5s-1 .672-1 1.5S9.448 8 10 8z"/>
                                                                                        </svg> : ''}
                                                </td>
                                                <td>{listValue.relationship}</td>
                                                <td>{listValue.priority ? listValue.priority : 'No / No conocido'}</td>
                                                <td>
                                                    <Editobmodal datos={datostablaOb[index]}></Editobmodal>
                                                    <Deleteobmodal datos={datostablaOb[index].name}></Deleteobmodal>
                                                </td>
                                            </tr>
                                        );
                                    })}
                                </tbody>
                            </Table>
                        </Tab.Pane>
                        <Tab.Pane eventKey="drugs">
                            <Card style={{ width: '18rem' }}>
                                <Card.Img variant="top" src="https://media.giphy.com/media/26tn33aiTi1jkl6H6/giphy.gif" />
                                <Card.Body>
                                    <Card.Title>Programando Drogas...</Card.Title>
                                    <Card.Text>
                                        Esta sección está aún en proceso. Por favor, ten paciencia. Solo soy una programadora.
                                    </Card.Text>
                                </Card.Body>
                            </Card>
                        </Tab.Pane>
                        <Tab.Pane eventKey="guns">
                            <Card style={{ width: '18rem' }}>
                                <Card.Img variant="top" src="https://media.giphy.com/media/26tn33aiTi1jkl6H6/giphy.gif" />
                                <Card.Body>
                                    <Card.Title>Programando Armas...</Card.Title>
                                    <Card.Text>
                                        Esta sección está aún en proceso. Por favor, ten paciencia. Solo soy una programadora.
                                    </Card.Text>
                                </Card.Body>
                            </Card>
                        </Tab.Pane>
                        <Tab.Pane eventKey="estate">
                            <Card style={{ width: '18rem' }}>
                                <Card.Img variant="top" src="https://media.giphy.com/media/26tn33aiTi1jkl6H6/giphy.gif" />
                                <Card.Body>
                                    <Card.Title>Programando Propiedades...</Card.Title>
                                    <Card.Text>
                                        Esta sección está aún en proceso. Por favor, ten paciencia. Solo soy una programadora.
                                    </Card.Text>
                                </Card.Body>
                            </Card>
                        </Tab.Pane>
                        <Tab.Pane eventKey="vehicles">
                            <Card style={{ width: '18rem' }}>
                                <Card.Img variant="top" src="https://media.giphy.com/media/26tn33aiTi1jkl6H6/giphy.gif" />
                                <Card.Body>
                                    <Card.Title>Programando Vehículos...</Card.Title>
                                    <Card.Text>
                                        Esta sección está aún en proceso. Por favor, ten paciencia. Solo soy una programadora.
                                    </Card.Text>
                                </Card.Body>
                            </Card>
                        </Tab.Pane>
                        </Tab.Content>
                    </Col>
                    </Row>
                    </Tab.Container>
                </div>
            );
        }else{
            return(
                <div></div>
            )
        }
    }
}

export default Evmenu;