import React from 'react';
import { Button, Modal, Image } from 'react-bootstrap';

function MyVerticallyCenteredModal(props) {

  var data = props.datos.datos;

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          {data.name}
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>        
        <p>
          <b>Nombre:</b><br></br>
          <div className="ml-3">{data.name ? data.name : 'No hay información.'}</div>       
        </p>
        <p>
          <b>Mafia o Banda:</b><br></br>
          <div className="ml-3">{data.mg ? data.mg : 'No hay información.'}</div>       
        </p>
        <p>
          <b>Descripción:</b>
          <div className="ml-3">{data.description ? data.description : 'No hay información.'}</div>  
        </p>
        <div className="text-center">
          {data.description_pic ? <Image src={data.description_pic} width="80%"></Image> : <strong>No hay foto.</strong>}
        </div>        
        <p>
          <b>Actitud:</b>
          <div className="ml-3">{data.attitude}</div>
        </p>
        <p>
          <b>Localización:</b>
          <div className="ml-3">{data.location ? data.location : 'No hay información sobre la localización.'}</div>  
        </p>
        <div className="text-center">
          {data.location_pic ? <Image src={data.location_pic} width="80%"></Image> : <strong>No hay foto.</strong>}
        </div>
        <p>
          <b>Vehículo</b>
          <div className="ml-3">{data.vehicle_desc ? data.vehicle_desc : 'No hay información sobre el vehículo.'}</div>
        </p>
        <p>
          {/* <b>Modelo del vehículo</b> */}
          <div className="ml-3">{data.vehicle_model ? data.vehicle_model : 'No hay información sobre el modelo del vehículo.'}</div>
        </p>
        <div className="text-center">
          {data.vehicle_pic ? <Image src={data.vehicle_pic} width="80%"></Image> : <strong>No hay foto.</strong>}
        </div>
        <p>
          <b>Aliados o Rivales:</b>
          <div className="ml-3">{data.friends ? data.friends : 'No hay información sobre las amistades o enemistades.'}</div>
        </p>
        <p>
          <b>Relación:</b>
          <div className="ml-3">{data.relationship}</div>    
        </p>
        <p>
          <b>Prioridad:</b>
          <div className="ml-3">{data.priority ? data.priority : 'No hay información sobre los rangos.'}</div>          
        </p>
        
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={props.onHide}>Cerrar</Button>
      </Modal.Footer>
    </Modal>
  );
}
  
function Infoobmodal(props) {
  const [modalShow, setModalShow] = React.useState(false);

  return (
    <>
      <Button variant="light" className="btn-sm" onClick={() => setModalShow(true)}>
        <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-eye-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path d="M10.5 8a2.5 2.5 0 1 1-5 0 2.5 2.5 0 0 1 5 0z"/>
          <path fillRule="evenodd" d="M0 8s3-5.5 8-5.5S16 8 16 8s-3 5.5-8 5.5S0 8 0 8zm8 3.5a3.5 3.5 0 1 0 0-7 3.5 3.5 0 0 0 0 7z"/>
        </svg>
      </Button>

      <MyVerticallyCenteredModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        datos={props}
      />
    </>
  );
}

export default Infoobmodal;